import Vue from 'vue';
import App from './App';
import VueRouter from 'vue-router';
import css from '../../components/css'; 

Vue.use(VueRouter);


const routes = [
    { path: '/css', component: css },
]
const router = new VueRouter({
    routes
})

// 
const appRouter = new Vue({
    router:router,
    render:h=>h(App)
}).$mount('#index');
  


